<template>
  <div>
    <el-card class="box-card">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>添加广告</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="box_border">
        <div class="group">
          <h4>添加广告</h4>
          <el-divider />
          <el-form ref="form" :rules="rules" :model="form" label-width="80px" label-position="left">
            <el-form-item label="名称" prop="name">
              <el-input v-model="form.name" />
            </el-form-item>
            <el-form-item label="* 图片" prop="imageUrl">
              <el-upload class="avatar-uploader" action="http-request" :http-request="uploadSectionFile" :show-file-list="false">
                <el-image v-if="form.imageUrl" :src="form.imageUrl" fit="contain" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon" />
              </el-upload>
              <p style="color: #999999; font-size: 10px">只能上传jpg/png文件，且不超过10MB，最多可上传5张</p>
            </el-form-item>
            <el-form-item label="地区" required>
              <areaSelectCmp2 :areaParent="form.area" @childFn2="onChildFn2"></areaSelectCmp2>
            </el-form-item>
            <el-form-item label="显示位置" required>
              <el-radio-group v-model="form.type">
                <el-radio :label="0">商城首页</el-radio>
                <el-radio :label="6">视频首页</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="类型" required>
              <el-radio-group v-model="form.advertisingType">
                <el-radio :label="0">商品</el-radio>
                <el-radio :label="1">链接</el-radio>
                <el-radio :label="2">店铺</el-radio>
                <el-radio :label="3">一分购</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item v-if="form.advertisingType === 0" label="商品" required>
              <el-select
                v-model="form.advertisingUrl"
                filterable
                remote
                placeholder="请选择"
                :remote-method="remoteMethod"
                :loading="loading"
              >
                <el-option
                  v-for="item in goodsList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-else-if="form.advertisingType === 1" label="链接" required>
              <el-select v-model="form.advertisingUrl" placeholder="请选择">
                <el-option
                  v-for="item in link"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item v-else-if="form.advertisingType === 2" label="店铺" required>
              <el-select
                v-model="form.advertisingUrl"
                filterable
                remote
                placeholder="请选择"
                :remote-method="onShopmanagequery"
                :loading="loading"
              >
                <el-option
                  v-for="item in shopManageQuery"
                  :key="item.id"
                  :label="item.shopName"
                  :value="item.id"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="排序" prop="sn">
              <el-input v-model.number="form.sn" />
            </el-form-item>
            <el-form-item size="medium">
              <el-button class="inquire" style="width: 100px" type="success" round @click="submitForm('form')">保存</el-button>
              <router-link :to="{ path: '/admin/adList' }"><el-button style="width: 100px" round>取消</el-button></router-link>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import areaSelectCmp2 from '@/components/area_select_cmp/index2.0.vue'
import { MIXINS_SHOPMANAGEQUERY_API } from '@/mixins/api.js'

export default {
  name: 'AdAdd',

  mixins: [
    MIXINS_SHOPMANAGEQUERY_API,
  ],

  data() {
    return {
      adId: '', // 编辑时获取的广告id
      loading: false,
      goodsList: [], // 商品列表
      shopManageQuery: [], // 店铺列表
      link: [
        { value: 'http://39.100.129.155:8099', label: '小超' },
        { value: 'http://h5.woho.city/milk_tea.html', label: '蜗视陪你过大年' },
        { value: 'http://h5.woho.city/breakfast_activities.html', label: '春节短视频大赛' },
      ],
      form: {
        name: '', // 名称
        imageUrl: '', // 编辑时回显的图片
        advertisingUrl: '', // 链接地址
        sn: '', // 序号
        advertisingType: 0,
        type: 0,
      },
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        advertisingUrl: [
          { required: true, message: '请输入链接地址', trigger: 'change' }
        ],
        sn: [
          { required: true, message: '请输入序号', trigger: 'blur' },
          { type: 'number', message: '序号必须为数字' }
        ],
        category: [
          { required: true, message: '请选择分类', trigger: 'change' }
        ]
      }
    }
  },

  components: {
    areaSelectCmp2,
  },

  created() {
    this.adId = this.$route.query.id
    if (this.$route.query.id) {
      // 编辑时的回显
      this._findAdData()
    }
  },

  methods: {
    // 自定义覆盖图片上传方法
    uploadSectionFile(params) {
      console.log('params', params)
      const isJPG = params.file.type === 'image/jpeg' || params.file.type === 'image/png'
      const isLt2M = params.file.size / 1024 / 1024 < 10
      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG/PNG 格式!')
        return
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 10MB!')
        return
      }
      const formData = new FormData()
      formData.append('file', params.file)
      this._upload(formData)
    },
    // 新增时提交表单
    submitForm(formName) {
      console.log(this.form)
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (!this.form.imageUrl) {
            this.$message.error('请上传图片')
            return false
          }
          if (this.adId) {
            // 编辑
            this._updateAd()
              .then((res) => {
                if (res.data.success) {
                  this.$message.success('保存成功')
                  this.$router.push('/admin/adList')
                } else {
                  this.$message.error(res.data.msg)
                }
              })
          } else {
            // 新增
            this._addAdItem()
              .then((res) => {
                if (res.data.success) {
                  this.$message.success('保存成功')
                  this.$router.push('/admin/adList')
                } else {
                  this.$message.error(res.data.msg)
                }
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 选择地区子组件向父组件传值
    onChildFn2(e) {
      this.form.area = e
    },
    // 选择商品
    remoteMethod (query) {
      if (query !== '') {
        this.loading = true
        this.getGoods(query)
      } else {
        this.options = []
      }
    },
    // 选择店铺
    async onShopmanagequery (query) {
      if (query !== '') {
        this.loading = true
        await this.apiShopmanagequery(query)
      } else {
        this.options = []
      }
    },

    // 接口api-----------------------------------------------------------
    // 添加广告
    _addAdItem() {
      const data = this.form
      data.platformType = 0
      return this.$http.post('/woho/advertising/manage/insert', data).then((res) => res)
    },
    // 上传图片
    _upload(formData) {
      this.$http.post('/common/oss/uploadFile', formData)
        .then((res) => {
          this.form.imageUrl = res.data.data.webUrl
        })
    },
    // 根据id查询
    async _findAdData() {
      this.$http.post(`/woho/advertising/manage/findById/${this.adId}`)
        .then(async (res) => {
          this.form = res.data
          this.form.area = res.data.area
          switch (res.data.advertisingType) {
            case 0:
              this.getGoods()
              break
            case 2:
              await this.apiShopmanagequery()
              break
          }
        })
    },
    // 编辑店铺广告
    _updateAd() {
      let data = this.form
      return this.$http.put('/woho/advertising/manage/update', data)
        .then((res) => res)
    },
    // 获取商品
    getGoods (query) {
      const data = {
        name: query,
        pageNo: 1,
        pageSize: 999,
      }
      this.$http.post('/woho/product/head/query', data).then(res => {
        this.loading = false
        this.goodsList = res.data.data.list
      })
    },
    // 获取店铺
    async apiShopmanagequery (query) {
      const data = {
        shopName: query,
        pageNo: 1,
        pageSize: 999,
        shopType: 0,
        approvalStatus: 1,
      }
      const res = await this.shopmanagequery(data)
      this.loading = false
      this.shopManageQuery = res.data.data.list
    },
  }
}
</script>

<style lang="scss" scoped>
  .box-card{
    min-width: 1100px;
    margin-top:15px;
    .box_border{
      margin: 70px auto;
      width: 740px;
      border-radius: 10px;
      border: solid 1px #bfbfbf;
      .group{
        width: 586px;
        margin: auto;
        .avatar-uploader .el-upload .el-icon-plus{
          border: 1px dashed #d9d9d9 !important;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          // overflow: hidden;
        }
        .avatar-uploader .el-upload:hover {
          border-color: #409EFF;
        }
        .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
        }
        .avatar {
          width: 178px;
          height: 178px;
          display: block;
        }
      }
    }
  }
</style>
